              .then((response) => {
                  this.temp = parseFloat(response.data['52']['temp'])
                  this.datum = (new Date(Date.parse(response.data['52']['date']))).toLocaleDateString('de-CH',{ day: '2-digit', month: '2-digit',year: 'numeric', hour: '2-digit', minute: '2-digit'  })
              })
      }
}
